/* > Baomoi */

@font-face {
    font-family: 'roboto';
    src: url('../fonts/roboto/Roboto_gdi.eot');
    src: url('../fonts/roboto/Roboto_gdi.eot?#iefix') format('embedded-opentype'),
        url('../fonts/roboto/Roboto_gdi.woff') format('woff'),
        url('../fonts/roboto/Roboto_gdi.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'roboto';
    src: url('../fonts/roboto/Roboto-Italic_gdi.eot');
    src: url('../fonts/roboto/Roboto-Italic_gdi.eot?#iefix') format('embedded-opentype'),
        url('../fonts/roboto/Roboto-Italic_gdi.woff') format('woff'),
        url('../fonts/roboto/Roboto-Italic_gdi.ttf') format('truetype');
    font-weight: normal;
    font-style: italic;
}

@font-face {
    font-family: 'roboto';
    src: url('../fonts/roboto/Roboto-Bold_gdi.eot');
    src: url('../fonts/roboto/Roboto-Bold_gdi.eot?#iefix') format('embedded-opentype'),
        url('../fonts/roboto/Roboto-Bold_gdi.woff') format('woff'),
        url('../fonts/roboto/Roboto-Bold_gdi.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}