/*
* TABLE OF CONTENTS
*
* header style
*/

// > header style
.header {
    background-color: #EBEBEB;

    .l-grid {
        position: relative;
        @include clearfix();
        height: 70px;
    }


    // tt event
    .logo-color {
        background-image: url('../img/mobile-logo-1.0.0.png');
        background-position: 0px 0px;
        background-size: 166px 42px;
    }

    .spr--hamburger {
        background-position: -50px -250px;
    }

    .spr--search {
        background-position: -50px -200px;
    }

    .l-grid {
        background: url(../img/header-background.png) center top no-repeat;

        @include breakpoint($iphone-6-plus) {
            background-size: cover;
        }
    }

    &__logo {
        display: block;
        font-size: 0;
        position: absolute;
        left: calc(50% - 73px);

        a {
            height: 70px;
            width: 160px;
            padding-top: 15px;
            display: block;
            overflow: hidden;
        }

        i {
            font-size: 0;
        }

        &:hover {
            opacity: .8;
        }
    }

    &__hamburger {
        position: absolute;
        left: 0;

        .btn {
            width: 42px;
            height: 70px;
            padding: 0;
            background-color: transparent;
            vertical-align: top;

            i {
                position: absolute;
                right: 10px;
                top: 23px;
                vertical-align: top;
            }
        }
    }

    &__search {
        position: absolute;
        right: 0;

        .btn {
            width: 42px;
            height: 70px;
            padding: 0;
            background-color: transparent;
            vertical-align: top;

            i {
                position: absolute;
                right: 10px;
                top: 23px;
                vertical-align: top;
            }
        }

        .search {
            display: none;
            margin-left: 15px;
            height: 30px;
            width: 230px;
            background: #FFFFFF;
            border-radius: 4px;
            overflow: hidden;
            color: #7f7f7f;

            >* {
                float: left;
            }

            button {
                width: 30px;
                height: 30px;
                border: none;
                padding: 0;
                background-color: transparent;
            }

            input {
                border: none;
                width: 200px;
                height: 30px;
                padding-left: 10px;
            }
        }
    }
}

.search-header {
    position: absolute;
    top: 70px;
    width: 100%;
    background: #cd0502;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    z-index: 100;
    border-top: 1px solid #ddd;
    height: 75px;
    display: none;

    input {
        width: calc(100%);
        font-size: 1.3rem;
        padding: 5px 50px  5px 10px;

    }

    button {
        position: absolute;
        background: transparent;
        right: 10px;
        top: 21px;
        .spr.spr--search{
            background-position: -0px -200px;
        }
    }

    &.is-active{
        display: flex;
    }
}